.mint_button_container, .disabled_mint_button_container {
    display: flex;
    flex-direction: row;
    margin-top: 2rem;
}

.mint_button_container button, .disabled_mint_button_container button {
    min-height: 30px;
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 20px;
    line-height: 28px;
    cursor: pointer;
    background: #408896;
    border: 1px solid #408896;
    border-radius: 6px;
    box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
    box-sizing: border-box;
    color: #FFFFFF;
    outline: 2px;
    padding: 0.5rem 3rem;
    text-align: center;
    text-rendering: geometricprecision;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    vertical-align: middle;
    text-align: center;
}

.mint_button_container button:active {
    opacity: .5;
    transform: translate(0, 1px);
}

.disabled_mint_button_container button {
    cursor: auto;
    background: transparent;
    color: #408896;
}

.counter_group {
    margin-left: 1rem;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  margin: 0; 
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: textfield;
}

@media(hover: hover) {
    .mint_button_container button:hover,
    .mint_button_container button:active {
        background-color: initial;
        background-position: 0 0;
        color: #408896;
    }
}

@media screen and (max-width: 650px) {
    .mint_button_container button, 
    .disabled_mint_button_container button, 
    input[type=number] {
        font-size: 16px;
        line-height: 24px;
    }
}



@media screen and (max-width: 400px) {
    .mint_button_container, .disabled_mint_button_container {
        display: flex;
        flex-direction: column;
    }
}