.gpt3__cta {
    padding: 2rem;
    margin: 8rem;
    border-radius: 1rem;
    background: var(--gradient-bar)
}

.gpt3__cta-content h3 {
    font-family: var(--font-family);
    font-size: 48px;
    font-weight: bolder;
}

@media screen and (max-width: 1000px) {
    .gpt3__cta {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__cta {
        margin: 1rem;
    }
}