.gpt3__header {
    display: flex;
    flex-direction: row;
}

.gpt3__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}
.gpt3__header-content h4 {
    font-family: var(--font-family);
    font-weight: 800;
}

.gpt3__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-text);
    margin-top: 1.5rem;
}

.gpt3__header-content__input {
    margin: 2rem 0 1rem;
    display: flex;
    flex-direction: row;
}

#plus {
    border-radius: 0 6px 6px 0;
    padding: 0.5rem 1rem;
}

#minus {
    border-radius: 6px 0 0 6px;
    padding: 0.5rem 1rem;
}

.num_input {
    background: transparent;
    max-width: 4rem;
    min-height: 30px;
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 20px;
    line-height: 28px;
    border: 1px solid #408896;
    color: var(--color-text);
    box-sizing: border-box;
    outline: 2px;
    padding: 0.5rem 0.2rem;
    text-align: center;
    text-rendering: geometricprecision;
    text-transform: none;
    user-select: none;
    -webkit-user-select: none;
    vertical-align: middle;
    text-align: center;
}



.gpt3__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-image img {
    width: 100%;
    height: auto;
}

@media screen and (max-width: 1440px) {
    .gpt3__header-image img {
        width: 100%;
        height: auto;
    }
}

@media screen and (max-width: 1050px) {
    .gpt3__header {
        flex-direction: column;
    }

    .gpt3__header-content {
        margin: 0 0 3rem;
    }
    .gpt3__header-image img {
        margin-top: 2rem;
        width: 70%;
        height: auto;
    }
}


@media screen and (max-width: 850px) {
    .gpt3__header-image img {
        width: 90%;
        height: auto;
        margin-top: 2rem;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .gpt3__header-content p {
        font-size: 16px;
        line-height: 24px;
    }
    
    .gpt3__header-content__people {
        flex-direction: column;
    }

    .gpt3__header-content__people p {
        margin: 0;
    }

    .gpt3__header-content__input input {
        font-size: 16px;
        line-height: 24px;
    }
    .gpt3__header-image img {
        width: 100%;
        height: auto;
    }
}

@media screen and (max-width: 490px) {
    .gpt3__header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .gpt3__header-content p {
        font-size: 14px;
        line-height: 24px;
    }

    .gpt3__header-content__input input {
        font-size: 12px;
        line-height: 16px;
    }
    .gpt3__header-image img {
        width: 100%;
        height: auto;
    }
}

@media screen and (max-width: 400px) {
    .counter_group {
        padding-top: 1rem;
        margin-left: 0;
    }
    .gpt3__header-content__input {
        flex-direction: column;
    }
    .num_input {
        padding: 0.5rem 1rem;
    }
    
}