.gpt3__possibility {
    display: flex;
    flex-direction: row;
}

.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__possibility-image img {
    width: 50%;
    height: auto;
    margin-top: 1rem;
}

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

}

.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}

.gpt3__possibility-content h4:last-child {
    color: #ff8A71;
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}

.gpt3__possibility-content li {
    color: var(--color-text);
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
}

#nested_list {
    margin-left: 2rem;
}

@media screen and (max-width: 2560px) {
    .gpt3__possibility-image img {
        width: 70%;
        height: auto;
        margin-left: 15%;
    }
}

@media screen and (max-width: 1024px) {
    .gpt3__possibility-image img {
        width: 100%;
        height: auto;
        margin-left: 0;
    }
}

@media screen and (max-width: 750px) {
    .gpt3__possibility-image img {
        width: 80%;
        height: auto;
        margin-left: 10%;
    }
    .gpt3__possibility {
        display: flex;
        flex-direction: column-reverse;
    }
    #collaborate_reverse {
        flex-direction: column;
    }
}