.connect_button {
    min-height: 30px;
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 20px;
    line-height: 28px;
    cursor: pointer;
    background: #408896;
    border: 1px solid #408896;
    border-radius: 6px;
    box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
    box-sizing: border-box;
    color: #FFFFFF;
    outline: 2px;
    padding: 0.5rem 3rem;
    text-align: center;
    text-rendering: geometricprecision;
    user-select: none;
    -webkit-user-select: none;
    vertical-align: middle;
    text-align: center;
}

.connect_button:active {
    opacity: .5;
    transform: translate(0, 1px);
}

#processing,
#connected {
    cursor: auto;
}

#processing {
    padding: 0.5rem 4.97rem;
}

#connected {
    color: #408896;
    background: transparent;
}

#connected:active {
    opacity: 1;
    transform: translate(0, 0);
}

#connected span {
    padding: 0;
    font-size: 16px;
}

.error_display_container {
    display: flex;
    flex-direction: column;
}

.connect_error_message {
    min-height: 30px;
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 12px;
    background: transparent;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
    box-sizing: border-box;
    color: #408896;
    text-align: left;
    cursor: auto;
    margin-top: 0.2rem;
}

@media(hover: hover) {
    .connect_button:hover {
        background-position: 0 0;
        background-color: initial;
        color: #408896;
    }
    #processing:hover {
        color: white;
        background: #408896;
    }
}

@media screen and (max-width: 1200px) {
    .connect_button {
        padding: 0.5rem 1rem;
    }
    
}

@media screen and (max-width: 1070px) {
    .connect_button {
        padding: 0.5rem 0.4rem;
    }
    
}