.gpt3__footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.gpt3__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    text-align: left;
}

.gpt3__footer-links div {
    margin: 1rem;
    flex-direction: row;
}

.gpt3__footer-links_logo {
    display: flex;
    flex-direction: row;
}

.gpt3__footer-links_logo img {
    width: 60px;
    height: auto;
}

.gpt3__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: bolder;
    margin-top: 0.7rem;
}

.gpt3__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.gpt3__footer-links_div h4 {
    font-size: 14px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #fff;
    margin: 0.4rem;
    margin-right: 1.5rem;
}

.gpt3__footer-links_div p {
    font-size: 12px;
    line-height: 15px;
    font-family: var(--font-family);
    color: #fff;
    margin: 0.5rem 0.5rem;
    cursor: pointer;
}

#closed_link {
    color: grey;
    pointer-events: none;
}

.gpt3__footer-copyright {
    text-align: center;
    width: 100%;
}

.gpt3__footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 910px) {
    .gpt3__footer-links div {
        flex-direction: column;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
    .gpt3__footer-links div {
        margin: 1rem 0;
    }
    .gpt3__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .gpt3__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}