.gpt3__blog {
    display: flex;
    flex-direction: column;
    padding: 4rem;
}

.gpt3__blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 3rem;
}

.gpt3__blog-heading h1 {
    font-size: 48px;
    line-height: 48px;
    font-family: var(--font-family);
    font-weight: 800;
}

.gpt3__blog-container {
    display: flex;
    flex-direction: row;
}

.gpt3__blog-container_groupB {
    display: flex;
    column-gap: 2rem;
}

@media screen and (max-width: 1200px) {
    .gpt3__blog-container_groupB {
        grid-gap: 2rem;
    }
}

@media screen and (max-width: 990px) {
    .gpt3__blog-container {
        flex-direction: column-reverse;
    }
    .gpt3__blog-container_groupB {
        flex: 1;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 2rem;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__blog-container_groupB {
        flex: 1;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 2rem;
    }
    .gpt3__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__blog {
        padding: 1rem;
    }
    .gpt3__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
    
}