.gpt3__blog-container_article {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.gpt3__blog-container_article-image {
    width: 100%;
    height: 100%;
    background: var(--color-bg);
}

.gpt3__blog-container_article-image img {
    width: 100%;
    height: 100%;
}

.gpt3__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.gpt3__blog-container_article-content p {
    font-family: var(--font-family);
    font-size: 14px;
    font-weight: 700;
    color: var(--color-text);
    text-align: center;
    margin-top: 1rem;
}

.gpt3__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-size: 22px;
    font-weight: 800;
    line-height: 30.30px;
    color: #fff;
    text-align: center;
}


@media screen and (max-width: 1440px) {
    .gpt3__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 28px;
    }
    
}

@media screen and (max-width: 1100px) {
    .gpt3__blog-container_article-content h3 {
        font-size: 16px;
        line-height: 22px;
    }
}

@media screen and (max-width: 990px) {
    .gpt3__blog-container_article-content h3 {
        font-size: 24px;
        line-height: 30px;
    }
    .gpt3__blog-container_article-content p {
        font-size: 16px;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__blog-container_article-content h3 {
        font-size: 28px;
        line-height: 30px;
    }
    .gpt3__blog-container_article-content p {
        font-size: 18px;
    }
}